.home-button {
    background-color: lightcoral;
    padding: 1%;
    text-align: left;
}

.detailed-header {
    margin-left: 40%;
    margin-right: 40%;
    text-align: center;
    color: rgb(0, 119, 255);
    border-color: rgb(62, 152, 255);
    border-style: solid;
    padding: 0.5%;
    background-color: white;
}

.top button{
    /*You need to specify the class name the App.css file*/
    /*below does pointer hand icon*/
    cursor: pointer;
    /* This floats the buttons side by side*/
    float:left;
}
.top button:not(:last-child){
    /* This prevents double borders from the right button*/
    border-right:none;
}
/* This focuses on the top of the screen and making the buttons work together */
.compass{
    height: 1%;
    display: flex;
    padding: 2% 1%;
}
/* Below focuses on the text for the list of questions (listOQues) */
.listOQues Form{
    padding: 10px 20px;
}


/* This is working with the size of the textbox --- Later I want to implement the horizontal scrolling */
.textbox{
    width: 100%; /* not changeable width */
    height: 100%; /*  */
    padding: 20px; /* Inner spacing */
    font-size: 11px; /* Text size */
    text-align:left;
    overflow-x: none;
    overflow-y: scroll;
    border: 1px solid black;
    border-radius: 4px;
    margin-left: 50px;
}
.listOQues text{
    font-size: x-large;
}
.top{
    flex: 50%;
    position:absolute;
    top: 3%;
    left: 50%;
    transform: translate(-50%, -50%);

}
.submitButton{
    padding: 20px 25px;
}
.top:after{
    content: "";
    clear:both;
    display: table;
}

.progress-section-d {
    position: fixed;
    text-align: center;
    font-size: 20px;
    left: 1%;
    padding-top: 0.5%;
    padding: 1%;
    background-color: lightblue;
    border-color: lightblue;
    border-style: solid;
    border-radius: 15px;
}

.progress-section-d-completed {
    position: fixed;
    text-align: center;
    font-size: 20px;
    left: 1%;
    padding-top: 0.5%;
    padding: 1%;
    background-color: lightgreen;
    border-color: lightgreen;
    border-style: solid;
    border-radius: 15px;
}

.lowerContainer{
    display:flex;
    align-items: center;
    width: 90%;
    height: 14%;


}

.lowerContainer row{
    border: 1px solid black;
}


.IMG{
    width: 50%;
    height: 200px;
    transform: translate( 100%, -1%);
}


.textboxIndicator{
    position: relative;
    bottom: 12%;
    right: -52%;
    font-size: x-small;
    color: gray;
}

.popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    justify-content: center;
    text-align: center;
    align-items: center;
    border-style: inset;
    border-radius: 5%;
    background-color: rgba(254, 187, 187, 0.85);
    padding: 1%;
    padding-left: 2%;
    padding-right: 2%;
    border-color: rgb(156, 35, 35);
    font-family: 'Dancing Script', cursive;
}

.popup-header {
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: rgb(156, 35, 35);
}

.popup-desc {
    padding: 5%;
    padding-top: 2%;
    padding-bottom: 2%;
    color: rgb(156, 35, 35);
}

.popup-buttons {
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.submit-button {
    background-color: rgb(156, 35, 35);
    border-color: black;
    margin-right: 5%;
}

.submit-button:hover {
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(136, 28, 28, 0.8);
    background-color: rgb(134, 22, 22);
    border-color: black;
}

.skip-button {
    background-color: lightgrey;
    border-color: black;
    color: black;
    margin-left: 5%;
}

.skip-button:hover {
    transform: scale(1.04);
    box-shadow: 0 0 20px rgb(169, 169, 169);
    background-color: lightgrey;
    border-color: black;
    color: black;
}